.addMealContainer {
  /* border: 2px solid palegreen;   */
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.addMealSelectableList {
  width: 100%;
}

.addMealFormAndDetailsContainer {
  display: -ms-flexbox;
  display: flex;
}
.outerAddMealFormContainer {
  min-width: 30%;
}
.innerAddMealFormContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex: 1 1 0%;
      flex: 1 1 0%;
}
/******MealList.js********/
.addMealListItem {
  margin-top: 2%;
}
.listItem {
  -webkit-box-shadow: 4px -1px 4px #aaaaaa;
          box-shadow: 4px -1px 4px #aaaaaa;
}
/************************/
.addMealRaisedButton {
  margin-top: 3%;
  margin-bottom: 2%;
  -ms-flex-item-align: end;
      align-self: flex-end;
}
/******Search and Chips********/
.searchAndChipsContainer {
  /* border: 2px solid red;      */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left: 4%;
  margin-top: -7%;
}
/******Show details********/
.detailsContainer {
  -ms-flex: 2 1 0%;
      flex: 2 1 0%;
}

/*Divider*/
.verticalDivider {
  border-right: 2px solid #d0d9db;
  margin-left: 4%;
  margin-right: 4%;
}

@media screen and (max-width: 800px) {
  .addMealFormAndDetailsContainer {
    -ms-flex-direction: column;
        flex-direction: column;
  }
  .searchAndChipsContainer {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin-left: 1%;
    margin-top: -1%;
  }
}
@media screen and (min-width: 801px) and (max-width: 1600px) {
  .smallStatistic {
    width: 100%;
  }
  .smallStatisticContainer .statisticIcons {
    display: -ms-flexbox;
    display: flex;
    padding-right: 7%;
  }

  .addMealAutocomplete hr {
    width: 90% !important;
  }
}



