/* chip */
.chip {
  min-width: 150px;
  display: -ms-flexbox;
  display: flex;
}
.chip span {
  -ms-flex: 2 1 0%;
      flex: 2 1 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
}

.chipContentContainer {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.chipContentContainer div {
  margin-right: 5%;
}
.chipItem {
  margin-bottom: 10px;
  margin-right: 1%;
}
.chip-quantity {
  display: -ms-flexbox;
  display: flex;
}

/* form */
.eachFormContainer {
  border: 2px solid rgba(103, 205, 230, 0.7);
  background-color: rgba(103, 205, 230, 0.06);
  border-style: ridge;
  border-radius: 4px;
  max-width: 230px;
  min-width: 210px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
}
.formContentContainer {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.form-name-time,
.form-name-quantity {
  margin-left: 2%;
}
.form-name-time div div {
  width: 108px !important;
}
.form-quantity {
  margin-left: 4%;
}
.form-quantity div div {
  width: 50px !important;
}
.eachFormContainer .icons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: baseline;
      align-items: baseline;
  margin-top: -5%;
}
.form-timePicker .importantChange {
  /* border: 2px solid red; */
  width: 60px !important;
}
.addMealAutocomplete div div {
  width: 234px !important;
}



